/* eslint-disable class-methods-use-this */
export default class SearchView {
  constructor() {
    this.document = document.body;
  }

  render() {
    const search = document.getElementById('search');

    const searchBox = document.createElement('div');
    searchBox.className = 'search-box';
    search.appendChild(searchBox);

    const searchImg = document.createElement('img');
    searchImg.className = 'search-img';
    searchImg.setAttribute('src', 'https://cdn.discordapp.com/attachments/336565794165227520/576921165478756352/3-search-cat_icon-icons.com_76679.png');
    searchBox.appendChild(searchImg);

    const searchInput = document.createElement('input');
    searchInput.className = 'search-input';
    searchInput.setAttribute('type', 'text');
    searchInput.setAttribute('id', 'search-input');
    searchInput.setAttribute('placeholder', '. . .');
    searchBox.appendChild(searchInput);
  }
}
