export default class ContentModel {
  constructor(state) {
    this.state = state;
    this.snippetData = {};
    this.staticticData = {};
  }

  async getData() {
    const { snippetUrl } = this.state;
    const response = await fetch(snippetUrl);
    this.snippetData = await response.json();
  }

  async getViewsData() {
    let listOfIds = this.snippetData.items.reduce((acc, clip) => `${acc},${clip.id.videoId}`);
    listOfIds += '&part=statistics';
    const { idUrl } = this.state;
    const response = await fetch(`${idUrl}${listOfIds}`);
    this.staticticData = await response.json();
  }

  getClipTitles() {
    return this.snippetData.items.map(clip => clip.snippet.title);
  }

  // eslint-disable-next-line class-methods-use-this
  getClipViewsCounters() {
    return this.staticticData.items.map(clip => clip.statistics.viewCount);
  }

  getClipDescriptions() {
    return this.snippetData.items.map(clip => clip.snippet.description);
  }

  getClipLinks() {
    return this.snippetData.items.map(clip => `https://www.youtube.com/watch?v=${clip.id.videoId}`);
  }

  getClipImages() {
    return this.snippetData.items.map(clip => clip.snippet.thumbnails.medium.url);
  }

  getClipDates() {
    return this.snippetData.items.map(clip => clip.snippet.publishedAt);
  }

  getChannelTitles() {
    return this.snippetData.items.map(clip => clip.snippet.channelTitle);
  }
}
