/* eslint-disable class-methods-use-this */
export default class AppView {
  constructor() {
    this.document = document.body;
  }

  clearContent() {
    const contentList = document.getElementById('content-list');
    while (contentList.lastChild) {
      contentList.removeChild(contentList.lastChild);
    }
    /*
    const navigation = document.getElementById('content-list');
    navigation.removeChild(navigation.lastChild);
    */
  }

  render() {
    const mainWrapper = document.createElement('div');
    mainWrapper.className = 'main-wrapper';
    this.document.appendChild(mainWrapper);

    const search = document.createElement('section');
    search.className = 'search';
    search.setAttribute('id', 'search');
    mainWrapper.appendChild(search);

    const content = document.createElement('section');
    content.className = 'content';
    content.setAttribute('id', 'content');
    mainWrapper.appendChild(content);

    const contentList = document.createElement('div');
    contentList.className = 'content-list';
    contentList.setAttribute('id', 'content-list');
    content.appendChild(contentList);

    const navigation = document.createElement('section');
    navigation.className = 'navigation hidden';
    navigation.setAttribute('id', 'navigation');
    mainWrapper.appendChild(navigation);
  }
}
