/* eslint-disable no-unused-expressions */
/* eslint-disable prefer-const */
/* eslint-disable max-len */
import SearchModel from '../models/SearchModel';
import ContentModel from '../models/ContentModel';
import AppView from '../views/AppView';
import SearchView from '../views/SearchView';
import ContenView from '../views/ContentView';
import NavigationView from '../views/NavigationView';

export default class App {
  constructor() {
    this.state = {
    };
  }

  async start() {
    const search = new SearchModel();
    this.state = search.getUrls();

    let clipsOnPage = 4;
    let page = 1;
    let elementWidth = 320;
    const appView = new AppView();
    const searchView = new SearchView();
    const navigationView = new NavigationView();

    appView.render();
    searchView.render();
    navigationView.render();

    const input = document.getElementById('search-input');
    const leftButton = document.getElementById('left-button');
    const rightButton = document.getElementById('right-button');
    const centerButton = document.getElementById('center-button');
    centerButton.textContent = page;

    function scroll(direction) {
      const slider = document.getElementById('content-list');
      if (direction === 'left' && page > 1) {
        slider.scrollLeft -= (elementWidth * clipsOnPage);
        page -= 1;
        if (page === 1) {
          leftButton.className += ' hidden';
        }
        centerButton.textContent = page;
      } else if (direction === 'right') {
        page += 1;
        if (page * clipsOnPage > 15) {
          alert('woops');
        }
        slider.scrollLeft += (elementWidth * clipsOnPage);
        centerButton.textContent = page;
        leftButton.classList.remove('hidden');
      }
    }

    input.onchange = async function onchangeSearch() {
      appView.clearContent();
      search.setUrls(input.value);
      this.state = search.getUrls();
      const model = new ContentModel(this.state);
      await model.getData();
      await model.getViewsData();
      const titles = model.getClipTitles();
      const descriptions = model.getClipDescriptions();
      const links = model.getClipLinks();
      const images = model.getClipImages();
      const dates = model.getClipDates();
      const channels = model.getChannelTitles();
      const viewCounts = model.getClipViewsCounters();
      const contentView = new ContenView(titles, links, images, dates, channels, descriptions, viewCounts);
      if (page !== 1) {
        leftButton.className += ' hidden';
        page = 1;
        centerButton.textContent = page;
      }
      contentView.render();

      let isDown = false;
      let startX;
      let x;

      const slider = document.getElementById('content-list');

      slider.addEventListener('mousedown', (e) => {
        isDown = true;
        slider.classList.add('active');
        startX = e.pageX - slider.offsetLeft;
      });

      slider.addEventListener('mouseleave', () => {
        isDown = false;
        slider.classList.remove('active');
      });
      slider.addEventListener('mouseup', () => {
        isDown = false;
        slider.classList.remove('active');
        (x - startX > 0) ? scroll('left') : scroll('right');
      });
      slider.addEventListener('mousemove', (e) => {
        if (!isDown) return;
        e.preventDefault();
        x = e.pageX - slider.offsetLeft;
      });

      const navigation = document.getElementById('navigation');
      navigation.classList.remove('hidden');
    };

    leftButton.addEventListener('click', () => {
      scroll('left');
    });

    rightButton.addEventListener('click', () => {
      scroll('right');
    });
  }
}
